@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid transparent; /* Black */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}

