/* Previous Page */

.root-pre{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
  padding-top: 10vh;
  padding-bottom: 10px; 
}

.p-header{
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.p-header p{
  font-size: 20px;
  box-shadow: 0 0 3px black;
  color: #000000;
  padding: 10px;
  border-radius: 20px;
  font-size: 20px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}


.content-table{
  width: 100%;
  /* max-height: 80vh; */
  border-collapse: collapse;
  font-size: 12px;
  min-width: 400px;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.45);
  background-color: white;
}

.content-table thead tr{
  font-size: 15px;
  color: #ffffff;
  background-color: rgb(22, 111, 195);
  border: 2px solid rgb(22, 111, 195);
  text-align: center;
}

.content-table th, .content-table td{
    padding: 12px 17px;
    border-bottom: 2px solid rgb(22, 111, 195);
    font-weight: 500;
}

.content-table tbody{
  max-height: 80vh;
  overflow-y: scroll;
}
.content-table tbody tr{
  font-size: 13px;
  text-align: center;
   border: 2px solid rgb(22, 111, 195);
}

.content-table tbody tr:last-of-type{
     border-bottom: 2px solid #009879;
}

