body,html{
    height: 100%;
    font-family: "Poppins", sans-serif;
}

.login-div{
    /* height: 90vh; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: center; 
    background-image: url("../../public/mainbg.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    height: 91vh;
}


.login-headers{
  height: 9vh;
  width: 100vw;
  background-color: white;
  box-shadow: 2px 2px 2px gray;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  padding-left: 30px;
}

.sl-nav{
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  
  cursor: pointer;
  
}

/* Login spinner animation */
.overlay-loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.container {
  width: 15vw;  /* Adjust the size as needed */
  height: 15vh; /* Adjust the size as needed */
  background: none;
}

.loading-text {
  color: #fff;
  font-size: 20px;
  margin-top: 20px;
}


.left-div{
    display: flex;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 75vh;
    z-index: 3;
    /* color: rgb(0, 0, 0); */
    /* padding: 20px; */
    /* background-color: rgba(163, 169, 169, 0.791); */
    /* background-image: linear-gradient(90deg,#0e2428,#3b2550); */
    /* background-image: linear-gradient(90deg,#b8bcbd,#c6b8d1); */
    /* background-color: white; */

    /* box-shadow: 0px 0px 2px 1px rgb(255, 255, 255); */
    /* border: 1px solid black; */
    transition: 1s;
}
.left-div img{
    /* height: 600px;
    width: 550px; */
    height: 100%;
    width: 100%;
    /* background-color: white; */
    /* padding: 8px; */
    /* border-radius: 15px; */
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    /* box-shadow: 0px 0px 5px rgb(255, 255, 255); */
}
.left-div span{
    font-size: 45px;
    font-weight: 700;
    color: #e8e8e8;

    text-shadow: 2px 2px 2px rgb(85, 104, 171);
    /* text-decoration: underline; */
}
.left-div p{
    font-size: 35px;
    color: #e8e8e8;
    text-shadow: 2px 2px 2px rgb(19, 32, 80);
    /* text-shadow: 2px 2px 2px rgb(255, 255, 255); */
    font-weight: 700;
}
.inner-div{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    /* box-shadow: 0px 0px 2px 1px rgb(255, 255, 255); */
    height: 75vh;
    color: white;
    padding: 15px;
    background-color: #29375c;
    transition: 1s;
}
.inner-div:hover{
    /* box-shadow: 0px 0px 7px rgb(40, 47, 49); */
    border: none;
    /* translate: 2px 2px; */
}
.inner-div img{
    height: 80px;
    width: 80px;
}
.inner-div span{
    font-size: 40px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}
.inner-div p{
    font-weight: 400;
    font-size: small;
    margin-top: -10px;
}

.userid{
     margin: 5px;
}
.userid p{
    font-weight: 400;
    margin-left: 10px;
    margin-bottom: 5px;
}

.userid input{
    width: 350px;
    height: 50px;
    padding: 5px 20px;
    border-radius: 30px;
    box-shadow: 0 0 1px rgb(255, 255, 255);
    background-color: transparent;
    margin-top: 4px;
    outline:none;
    transition: 1s;
}
.userid input::placeholder{
    color: white;
}
.userid input:hover{
box-shadow: 0 0 5px rgb(255, 255, 255);
}
