  .icon{
    position: absolute;
    margin-right: 25px;
  }

  .lbtn {
    display: flex;
    justify-content: center;
    padding: 10px;
    border: unset;
    border-radius: 30px;
    color: #212121;
    z-index: 1;
    background: #e8e8e8;
    position: relative;
    width: 350px;
    font-weight: 500;
    font-size: 17px;
    -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
    box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
    transition: all 150ms;
    overflow: hidden;
    margin: -5px;
   }
   
   .lbtn::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    border-radius: 5px;
    background-color: rgb(117, 161, 238);
    z-index: -1;
    -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
    box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
    transition: all 200ms;
   }
   
   .lbtn:hover {
    color: #e8e8e8;
   }
   
   .lbtn:hover::before {
    width: 100%;
   }

  .lbtn2 {
